/* ======================================================================== */
/* 3. button */
/* ======================================================================== */
.button {
  position: relative;
  display: inline-block;
  font-family: var(--font-primary);
  font-size: calc(var(--subheading-min-font-size) * 1px);
  font-weight: bold;
  line-height: var(--subheading-line-height);
  letter-spacing: 2px;
  color: #888888;
  text-transform: uppercase;
  font-weight: bold;
  text-align: center;
  transition: all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  cursor: pointer;
  outline: none;
  box-shadow: none;
  border-width: 2px;
  border-style: solid;
  padding: 16px 32px;
}

@media screen and (min-width: 320px) {
  .button {
    font-size: calc(var(--subheading-min-font-size) * 1px + (var(--subheading-max-font-size) - var(--subheading-min-font-size)) * ((100vw - 320px) / 1600));
  }
}

@media screen and (min-width: 1920px) {
  .button {
    font-size: calc(var(--subheading-max-font-size) * 1px);
  }
}

.button:focus {
  outline: none;
}

.button_accent {
  background-color: var(--color-accent-primary-2);
  border-color: var(--color-accent-primary-2);
  color: var(--color-accent-primary-4);
}

.button_accent .button__icon {
  background-color: var(--color-accent-primary-3);
  color: #ffffff;
}

.button_accent:hover {
  background-color: var(--color-accent-primary-3);
  color: #ffffff;
}

.button_accent:hover .button__icon {
  background-color: var(--color-accent-primary-4);
}

.button_icon {
  display: inline-flex;
  padding: 0;
  border: none;
}

.button__label {
  padding: 18px 32px;
}

.button__icon {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  width: 60px;
  transition: all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

.button__icon i {
  font-size: 24px;
}

.button_fullwidth {
  width: 100%;
}

@media screen and (max-width: 991px) {
  .button {
    padding: 12px 28px;
  }
  .button__label {
    padding: 12px 28px;
  }
  .button__icon {
    width: 43px;
  }
  .button__icon i {
    font-size: 20px;
  }
  .button_icon {
    padding: 0;
  }
}

