}

.comment-list > li {
  padding-top: 30px;
  padding-bottom: 30px;
}

.comment-list > li ol.children {
  list-style-type: none;
  padding: 0;
  margin: 0;
  padding-left: 4%;
}

.comment-list > li ol.children li {
  padding-top: 30px;
