/* ======================================================================== */
/* 27. inputFloat */
/* ======================================================================== */
.input-float {
  position: relative;
  display: block;
  width: 100%;
  margin-bottom: 0;
}

.input-float__label {
  position: absolute;
  top: 1em;
  left: 0;
  margin: auto;
  display: block;
  font-size: 16px;
  transition: all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  transform-origin: left center;
  cursor: text;
  color: #888888;
}

.input-float__input {
  display: block;
  width: 100%;
  width: 100%;
  border-bottom: 1px solid rgba(136, 136, 136, 0.3);
  border-top: none;
  border-right: none;
  border-left: none;
  outline: none;
  padding: 12px 0 8px;
  transition: all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  background-color: transparent;
  border-radius: 0;
  color: inherit;
}

.input-float__input_textarea {
  resize: none;
  min-height: 200px;
}

.input-float__input_focused + .input-float__label, .input-float__input_not-empty + .input-float__label {
  transform: scale(0.8571) translateY(-2rem);
}

.input-float__input_focused {
  border-color: var(--color-accent-primary-1);
}

.input-float__input_focused + .input-float__label {
  color: var(--color-accent-primary-1);
}

/* ======================================================================== */
/* 28. grid */
/* ======================================================================== */
.grid {
  overflow: hidden;
}

.grid__sizer {
  padding: 0 !important;
  margin: 0 !important;
  height: 0 !important;
}

.grid__item {
  display: block;
  width: 100%;
}

.grid__item-link {
  display: block;
  position: relative;
}

.grid__item-link img {
  transition: all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  transform-origin: center center;
}

.grid__item-link:hover, .grid__item-link:focus {
  opacity: 1;
}

.grid__item-link:hover img {
  transform: scale(1.1);
}

.grid__item-link:hover .grid__item-overlay {
  transform: scaleY(1);
}

.grid__item-link:hover .grid__item-icon {
  transform: translate(-50%, -50%);
  opacity: 1;
  visibility: visible;
  transition-delay: 50ms;
}

.grid__item-link:hover .grid__item-icon:after {
  transform: scale(1);
  transition-delay: 100ms;
}

.grid__item-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 80px;
  height: 80px;
  top: 50%;
  left: 50%;
  z-index: 50;
  transform: translate(-50%, 10px);
  transition: all 0.6s ease;
  opacity: 0;
  visibility: hidden;
  color: #ffffff;
}

.grid__item-icon:after {
  content: '';
  display: block;
  border-radius: 100%;
  border: 1px solid #888888;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transform: scale(0);
  transition: all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

.grid__item-icon-symbol {
  font-size: 28px !important;
}

.grid__item-overlay {
  opacity: 1;
  background-color: #181818;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  transition: all 0.3s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  transform: scaleY(0);
  transform-origin: bottom center;
}

.grid__item-icon {
  width: 60px;
  height: 60px;
}

.grid__item-icon-symbol {
  font-size: 24px !important;
}

