	transform: translate(-50%, 10px);
	transition: all 0.6s ease;
	opacity: 0;
	visibility: hidden;
	color: $white;
	&:after {
		content: '';
		display: block;
		border-radius: 100%;
		border: 1px solid $brown-grey;
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		transform: scale(0);
