/* ======================================================================== */
/* 97. widget */
/* ======================================================================== */
.widget {
	p {
		&:last-of-type {
			margin-bottom: 0; } }
	select {
		width: 100%;
		max-width: 100%; }
	ul {
		@include reset-ul;
		text-align: left;
		li {
			display: block;
			margin-bottom: 20px;
			&:last-child {
				margin-bottom: 0; } }
		ul {
			width: 100%;
			padding-left: 1em;
			margin-top: 1em; }
		.sub-menu {
			padding-left: 1em;
			margin-top: 1em; } }
	ul {
		li {
			a {
				color: $brown-grey;
				&:hover {
					color: $sandstone; } } } } }

.widgettitle {
	display: block;
	@include small-caps;
	color: $dusk;
	margin-bottom: 1.5em;
	&:before {
		content: '';
		display: block;
		width: 100%;
		height: 2px;
		background-color: $dusk;
		margin-bottom: 0.5em; } }

/* ======================================================================== */
/* 98. widgetArchive */
/* ======================================================================== */
.widget_archive {
	ul {
		li {
			display: flex !important;
			justify-content: space-between;
			align-items: center;
			flex-wrap: wrap;
			span {
				color: $brown-grey;
				font-size: 13px;
				line-height: 1; } } } }

/* ======================================================================== */
/* 99. widgetCalendar */
/* ======================================================================== */
#wp-calendar {
	width: 100%;
	text-align: center;
	caption {
		caption-side: top;
		width: 100%;
		text-align: center;
		padding-top: 0;
		padding-bottom: 10px; }
	th {
		font-weight: 600;
		padding: 5px;
		text-align: center; }
	td {
		padding: 5px;
		color: $brown-grey;
		&#next {
			text-align: right; }
		&#prev {
			text-align: left; } }
	tbody {
		a {
			&:before {
				content: '';
				position: absolute;
				top: 50%;
				left: 50%;
				width: 30px;
				height: 30px;
				background-color: $off-white;
				border-radius: 100%;
				transform: translate(-50%, -50%);
				z-index: -1;
				@include trans1; }
			&:hover {
				color: $white;
				&:before {
					background: $dusk; } } } }

	tr {
		border-bottom: none; }
	a {
		display: inline-block;
		position: relative;
		color: $dusk;
		z-index: 50;
		&:hover {
			color: $sandstone; } } }

/* ======================================================================== */
/* 100. widgetCassioLogo */
/* ======================================================================== */

/* ======================================================================== */
/* 101. widgetCassioMenuInline */
/* ======================================================================== */
.widget_cassio_menu_inline {
	ul {
		li {
			display: inline-block;
			margin-bottom: 0;
			a {
				padding: 4px;
				&:before {
					display: none; } } } } }

@media screen and (max-width: $md) {
	.widget_cassio_menu_inline {
		.menu {
			text-align: center; } } }

/* ======================================================================== */
/* 102. widgetCassioSocial */
/* ======================================================================== */
.widget_cassio_social {
	ul {
		text-align: inherit;
		li {
			display: inline-block;
			margin-bottom: 0; } } }

/* ======================================================================== */
/* 103. widgetCategories */
/* ======================================================================== */
.widget_categories {
	ul {
		li {
			display: flex !important;
			justify-content: space-between;
			align-items: center;
			flex-wrap: wrap;
			span {
				color: $brown-grey;
				font-size: 13px;
				line-height: 1; } } } }

/* ======================================================================== */
/* 104. widgetLogo */
/* ======================================================================== */
.widgetcassio_logo, .widget_cassio_logo {
	max-width: 360px;
	.logo {
		margin-bottom: 2em; } }

@media screen and (max-width: $md) {
	.widgetcassio_logo, .widget_cassio_logo {
		max-width: 100%;
		.logo {
			margin-bottom: 1em; } } }

/* ======================================================================== */
/* 105. widgetNavMenu */
/* ======================================================================== */
.widget_nav_menu {
	ul.menu {
		> li {
			margin-bottom: 1em !important;
			white-space: normal;
			word-break: break-word;
			a {
				display: inline-block;
				padding: 0;
				font-size: 16px;
				&:after, &:before {
					display: none; } } }
		> li.menu-item-has-children {
			margin-bottom: 1.5em !important;
			a {
				&:after {
					display: none; } } } }
	ul.sub-menu {
		background-color: transparent;
		position: relative;
		top: auto;
		left: auto;
		right: auto;
		opacity: 1;
		visibility: visible;
		box-shadow: none;
		border-bottom: none;
		transform: none;
		margin-top: 1em;
		> li {
			border-left: none;
			> a {
				padding: 3px 0;
				border-left: none;
				&:hover {
					background-color: unset; } } } } }

/* ======================================================================== */
/* 106. widgetPolylang */
/* ======================================================================== */
.widget_polylang {
	display: inline-block;
	font-size: 14px;
	select {
		width: auto;
		display: inline-block;
		padding: 5px 25px 5px 15px; }
	ul {
		@include reset-ul;
		li {
			display: inline-block;
			margin-left: 10px;
			margin-right: 10px;
			margin-bottom: 0; } } }

/* ======================================================================== */
/* 107. widgetRSS */
/* ======================================================================== */
.widget_rss {
	.rsswidget {
		font-family: $font-secondary;
		font-weight: bold;
		color: $dusk; }
	.rss-date {
		display: block;
		width: 100%;
		font-size: 13px;
		margin-top: 0.5em;
		margin-bottom: 1em;
		color: $brown-grey; }
	.rssSummary {
		margin-top: 1em;
		margin-bottom: 1em; }
	ul {
		> li {
			border-bottom: 1px solid $borders-dark;
			padding-bottom: 1em;
			&:last-child {
				border-bottom: none;
				padding-bottom: 0; } } } }

/* ======================================================================== */
/* 108. widgetRecentComments */
/* ======================================================================== */
.widget_recent_comments {
	ul {
		li {
			padding: 20px 0;
			margin-bottom: 0 !important;
			border-bottom: 1px solid $borders-dark;
			&:first-child {
				padding-top: 0; }
			&:last-child {
				padding-bottom: 0;
				border-bottom: none; }
			a {
				color: $dusk;
				font-family: $font-secondary;
				font-weight: bold;
				&:hover {
					color: $sandstone; } } } }
	.comment-author-link {
		a {
			color: $brown-grey;
			font-family: $font-primary;
			font-weight: normal; } } }

/* ======================================================================== */
/* 109. widgetRecentEntries */
/* ======================================================================== */
.widget_recent_entries {
	.post-date {
		display: block;
		width: 100%;
		font-size: 13px;
		margin-top: 0.25em;
		color: $brown-grey; }
	ul {
		li {
			padding: 20px 0;
			margin-bottom: 0 !important;
			border-bottom: 1px solid $borders-dark;
			a {
				font-family: $font-secondary;
				font-weight: bold;
				color: $dusk !important;
				&:hover {
					color: $sandstone !important; } }
			&:first-child {
				padding-top: 0; }
			&:last-child {
				padding-bottom: 0;
				border-bottom: none; } } } }

/* ======================================================================== */
/* 110. widgetWPML */
/* ======================================================================== */
.widget_icl_lang_sel_widget {
	display: inline-block;
	margin-bottom: 0;
	vertical-align: middle;
	font-size: 14px;
	.wpml-ls-legacy-dropdown {
		width: auto;
		border: 1px solid $borders-dark;
		border-radius: 2px;
		padding: 2px 5px;
		a {
			border: none;
			background: transparent;
			&:hover, &:focus {
				opacity: 1; } } }
	.wpml-ls-legacy-dropdown a:hover, .wpml-ls-legacy-dropdown a:focus, .wpml-ls-legacy-dropdown .wpml-ls-current-language:hover>a {
		background: transparent; }
	.wpml-ls-sub-menu {
		border-top: none; }
	.wpml-ls-legacy-list-horizontal {
		padding: 2px 5px;
		.wpml-ls-item {
			display: inline-block;
			margin-bottom: 0; } } }
.lang-switch-no-padding-right {
	.widget_icl_lang_sel_widget {
		.wpml-ls-legacy-list-horizontal {
			padding-right: 0; } } }
